@charset "UTF-8";
@import url("https://fonts.googleapis.com/css2?family=Inter:ital,wght@0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap");
@page {
  margin: 0.5in;
  size: letter;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.header,
.footer,
header,
footer {
  display: none !important;
}

* {
  font-family: "Inter", sans-serif !important;
}

html {
  font-family: "Inter", sans-serif !important;
  line-height: 1.3em;
  color: #222 !important;
  font-size: 13px;
}

body {
  font-family: "Inter", sans-serif !important;
  background: white !important;
  padding: 15px;
  max-width: 800px;
  margin: 0 auto;
  text-align: left;
  color: #222 !important;
}

code,
pre,
code *,
pre * {
  font-family: "SF Mono", Monaco, "Cascadia Code", monospace !important;
}

@media print {
  html {
    color: #222;
  }
  body {
    background: white;
    padding: 15px;
    max-width: 800px;
    margin: 0 auto;
    text-align: left;
  }
  h1 {
    font-size: 3rem;
    font-weight: 300 !important;
    font-family: "Inter", sans-serif !important;
    color: #808;
    margin: 0 0 0.75em 0;
    border-bottom: 0.25pt solid #808;
    padding-top: 0.5em;
    padding-bottom: 0.25em;
    line-height: 1.1;
  }
  h2 {
    font-size: 1.75rem;
    font-weight: 600 !important;
    font-family: "Inter", sans-serif !important;
    color: #808;
    margin: 0.5em 0 0.5em 0;
    border-bottom: 0.25px solid #808;
    padding: 0.75em 0 0.4em 0;
  }
  h3 {
    font-size: 1.5rem;
    font-weight: 600 !important;
    font-family: "Inter", sans-serif !important;
    color: #808;
    border-bottom: 0.25px solid #808;
    padding: 1em 0 0.5em 0;
  }
  h4 {
    font-size: 1.25rem;
    font-weight: 600 !important;
    font-family: "Inter", sans-serif !important;
    color: #808;
    margin: 1em 0 0.1em 0;
  }
  h5 {
    font-size: 1rem;
    font-weight: 800 !important;
    font-family: "Inter", sans-serif !important;
    color: #808;
    margin: 24px 0 4px 0;
    padding-bottom: 0.25em;
  }
  h6 {
    font-size: 0.7rem;
    font-weight: 800 !important;
    font-family: "Inter", sans-serif !important;
    color: #808;
    margin: 1em 0 0.25em 0;
    padding-bottom: 0.25em;
    text-transform: uppercase;
    letter-spacing: 0.05em;
  }
  p {
    margin: 10px 0;
    text-align: left;
  }
  ul,
  ol {
    margin: 8px 0 8px 25px;
  }
  ul li,
  ol li {
    margin: 0.2em 0;
  }
  strong,
  b {
    font-weight: bold !important;
    font-family: "Inter", sans-serif !important;
  }
  em,
  i {
    font-style: italic !important;
    font-family: "Inter", sans-serif !important;
  }
  blockquote {
    border-left: 4px solid #808;
    background: #f2e6f2;
    margin: 16px 0;
    padding: 0.25em 0.5em;
    font-style: italic;
    color: #222;
    border-radius: 0 4px 4px 0;
  }
  blockquote strong,
  blockquote b,
  em strong,
  em b,
  i strong,
  i b {
    font-family: "Inter", sans-serif !important;
    font-weight: bold !important;
    font-style: italic !important;
  }
  a {
    color: #880088;
    text-decoration: none;
    font-weight: 700;
  }
  code {
    background: #f7f7f7;
    border: 1px solid #e7e7e7;
    border-radius: 4px;
    padding: 2px 4px;
    font-weight: 700;
    color: #808;
    font-size: 0.75em !important;
  }
  pre {
    background: #f8f9fa;
    border: 1px solid #e9ecef;
    border-radius: 8px;
    padding: 0.5em;
    margin: 16px 0;
    overflow-x: auto;
    font-weight: 600;
    color: blue;
  }
  pre code {
    background: none;
    border: none;
    padding: 0;
    color: #24292e;
  }
  pre p {
    margin: 0;
    line-height: 1.4;
  }
  img {
    max-width: 100%;
    height: auto;
    display: inline-block;
    vertical-align: middle;
  }
  img[src$=".svg"] {
    width: 25px;
    height: 25px;
    display: inline-block;
    vertical-align: middle;
  }
  table {
    width: 100% !important;
    border-collapse: collapse !important;
  }
  table code,
  table pre {
    line-height: 1.2em;
  }
  table th {
    border-bottom: 0.25pt solid #999;
    padding: 0.4em 0.2em;
  }
  table td {
    border-bottom: 0.25pt solid #ccc;
    padding: 0.4em 0.2em;
    font-size: 0.9em;
  }
  .page-break {
    page-break-before: always !important;
    break-before: page !important;
    height: 0;
    margin: 0;
    padding: 0;
    border: none;
    visibility: hidden;
  }
  .icon-placeholder {
    display: inline-block;
    width: 20px;
    height: 20px;
    border-radius: 3px;
    margin: 2px;
  }
  .icon-5g {
    background-color: #006400;
  }
  .icon-1gig {
    background-color: #ff8c00;
  }
  .icon-10gig {
    background-color: #cc4400;
  }
  .icon-sonic {
    background-color: #27aae1;
  }
  .pdf-source {
    width: 100% !important;
    display: block !important;
    border: 0.5px solid #ccc !important;
    padding: 0.25em !important;
    border-radius: 2em !important;
    background-color: #f7f7f7 !important;
    color: #222 !important;
    margin: -1em auto 3em auto !important;
    text-align: center !important;
    box-sizing: border-box !important;
  }
  .pdf-source p {
    margin: 0 !important;
    text-align: center !important;
    padding: 0 !important;
    font: 600 0.8rem/1.2 "Inter", sans-serif !important;
  }
  p.live-site-shield {
    display: inline-block !important;
    background: none !important;
    padding: 0 !important;
    margin: 1em 0 !important;
    border-radius: 6px !important;
    font-size: 13px !important;
    text-align: left !important;
    overflow: hidden !important;
    white-space: nowrap !important;
    font-family: "Inter", sans-serif !important;
    line-height: 1 !important;
  }
  p.live-site-shield .label {
    background: #555 !important;
    color: white !important;
    padding: 8px 12px !important;
    font-weight: 500 !important;
    display: inline-block !important;
    margin: 0 !important;
    vertical-align: baseline !important;
    border-radius: 6px 0 0 6px !important;
    line-height: 1.2 !important;
  }
  p.live-site-shield .label::before {
    content: "üåê ";
    margin-right: 6px !important;
    vertical-align: baseline !important;
    display: inline-block !important;
  }
  p.live-site-shield .url {
    background: #808 !important;
    color: #ffffff !important;
    padding: 8px 12px !important;
    font-weight: bold !important;
    display: inline-block !important;
    margin: 0 !important;
    vertical-align: baseline !important;
    font-family: "SF Mono", Monaco, "Cascadia Code", monospace !important;
    border-radius: 0 6px 6px 0 !important;
    line-height: 1.2 !important;
    font-size: 13px !important;
  }
  p.live-site-shield .url,
  p.live-site-shield .url * {
    color: #ffffff !important;
    font-family: "SF Mono", Monaco, "Cascadia Code", monospace !important;
    font-weight: bold !important;
  }
}